<template>
    <el-row class="m-header-topbar m-header">
        <el-col :span="10">
            <geo />
        </el-col>
        <el-col :span="14">
            <m-nav />
        </el-col>
    </el-row>
</template>

<script>
import geo from './geo.vue';
import MNav from './nav.vue';
export default {
    components: {
        geo,
        MNav
    }
}
</script>
<style>
.m-header.m-header-topbar{
    width: 1190px!important;
    z-index: 9999;
    height: 40px;
    line-height: 40px;
    z-index: 9;
}
.m-header a{
    text-decoration: none;
}
</style>


