<template>
    <div class="m-header">
        <el-row>
            <top-bar />
        </el-row>
        <el-row>
            <search-bar /> 
        </el-row>
    </div>
</template>

<script>
import topBar from './topBar.vue'
import searchBar from './searchBar.vue'

export default {
    components: {
        topBar,
        searchBar
    }
}
</script>
<style>
.m-header .el-row:nth-child(1){
    width: 1190px;
    margin: 0 auto;
    z-index: 9999;
    height: 40px;
    /* line-height: 40px; */
}
.m-header .el-row:nth-child(2){
    background: #fff;
    z-index: 100;
}
.m-header .el-row:nth-child(2) .search-panel{
    width: 1190px;
    margin: 0 auto;
}
</style>


