<style>
.m-nav{
    float: right;
    margin: 0;
    padding: 0;
    z-index: 999;
}
.m-nav .nav{
    list-style: none;
    float: right;
    height: 40px;
    margin: 0;
}
.m-nav .nav li{
    float: left;
    width: 78px;
    box-sizing: border-box;
    text-align: center;
    cursor: pointer;
}
.m-nav .nav li a{
