<template>
    <dl class="s-item">
        <dt>
            <img :src="meta.image" alt="图片">
        </dt>
        <dd>
            <h3>
                <router-link :to="{name: 'productDeatil'}">{{meta.title}}</router-link>
            </h3>
            <p>
                <el-rate
                    v-model="meta.score"
                    disabled
                    show-score
                    text-color="#ff9900"
                    score-template="{value}"
                ></el-rate>
                <span v-if="meta.score > 4.5">很好</span>
                <span v-else-if="meta.score > 4">好</span>
                <span v-else-if="meta.score > 3.5">不错</span>
                <span v-else-if="meta.score > 3">一般</span>
                <span v-else>很差</span>
                <span>{{meta.score}}分</span>
                <span class="s-item-comment-total">{{meta.commentNum}}人评论</span>
            </p>
            <p>
                <span class="s-item-tab">
                    <span v-for="(v, i) in meta.tab" :key="v">
                        {{v + (i < meta.tab.length - 1 ? '|' : '')}}
                    </span>
                </span>
                <span class="s-item-addr">{{meta.address}}</span>
                <span>
                    <a href="#">
                        <i class="el-icon-location" />查看地图
                    </a>
                </span>
            </p>
            <p>
                <span class="s-item-price">
                    人均￥{{meta.avgPrice}}
                </span>
            </p>
            <ul class="deal-item">
                <li class="items" v-for="(item, index) in meta.dealItems.slice(0, 2)" :key="index">
                    <p class="deal-title">
                        {{item.title}}
                    </p>
                    <p>
                        <span class="deal-price">
                            ￥{{item.price}}
                        </span>
                        <span class="deal-old-price">
                            门市价￥{{item.counterPrice}}
                        </span>
                        <span class="deal-seles">
                            已售{{item.saleNum}}
                        </span>
                    </p>
                </li>
            </ul>
        </dd>
    </dl>
</template>
<script>
export default {
    props: ["meta"]
}
</script>
<style>
.s-item{
    display: flex;
    padding: 20px 0;
    margin: 0;
    border-top: 1px solid #E5E5E5;
    font-size: 14px;
    height: 226px;
}
.s-item dt{
    width: 220px;
    padding-right: 20px;
}
.s-item dt>img{
    width: 220px;
    height: 125px;
    overflow: hidden;
}
.s-item dd{
    flex: 1;
}
.s-item dd>h3{
    font-weight: 500;
    font-size: 16px;
    color: #333;
    margin: 0 0 7px 0;
}
.s-item dd>p{
    margin: 0;
}
.s-item dd .el-rate{
    display: inline-block;
}
.s-item dd .el-rate .el-rate__icon{
    font-size: 12px;
}
.s-item dd span{
    font-size: 12px;
    margin-right: 10px;
}
.s-item dd .s-item-comment-total{
    color: #f90;
}
.s-item dd .s-item-price {
    color: #666;
}
.s-item dd .s-item-tab span{
    margin: 0;
}
.s-item dd span a{
    text-decoration: none;
    color: #222;
}
.s-item dd .el-icon-location{
    color: rgb(216, 235, 47);
}


.s-item dd ul{
    height: 110px;
    list-style: none;
    /* font-size: 12px; */
    margin-top: 20px;
    border-top: 1px dashed #ddd;
}
.s-item>dd>ul .items{
    cursor: pointer;
    border-radius: 4px;
    padding: 6px 0 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.s-item dd>ul li p{
    margin: 0;
}
.s-item dd>ul .items .deal-title{
    font-size: 14px;
    color: #666;
}
.s-item dd>ul .items .deal-title:hover{
    color: rgb(248, 184, 8);
}
.s-item dd>ul .items p .deal-price{
    font-size: 16px;
    color: #f60;
}
.s-item dd>ul .items p .deal-old-price{
    color: #999;
}
.s-item dd>ul .items p .deal-sales{
    color: #999;
}







</style>


