<template>
    <div class="m-hcity">
        <dl>
            <dt>{{ title }}</dt>
            <dd v-for="(item, index) in list" :key="index">
                {{ item }}
            </dd>
        </dl>
    </div>
</template>

<script>
export default {
    props: [
        "title",
        "list"
    ]
}
</script>

<style>
.m-hcity dl{
    display: flex;
}
.m-hcity dl dt{
    font-size: 16px;
    color: #333;
    font-weight: 500;
}
.m-hcity dl dd{
    margin: 0 20px;
    color: #666;
    line-height: 22px;
    font-size: 14px;
}
.m-hcity dl dd:hover{
    color: #31BBAC;
    cursor: pointer;
}
</style>


