<template>
    <div class="m-menu">
        <dl class="nav" @mouseleave="menuLeave">
            <dt>全部分类</dt>
            <dd v-for="(item, index) in menuList" :key="index" @mouseenter="menuEnter(item)">
                <i :class="item.type" />
                {{ item.name }}
                <span class="arrow"></span>
            </dd>
        </dl>
        <div v-if="curDetail" class="detail" @mouseenter="detailEnter" @mouseleave="detailLeave">
            <template v-for="(item,index) in curDetail.items">
                <h4 :key="index">{{ item.title }}</h4>
                <span v-for="(v, i) in item.items" :key="i+'_'">{{ v }}</span>
            </template>
        </div>
    </div>
</template>
<script>
import api from '@/api/index.js';
export default {
    data() {
        return {
            menuList: [],
            curDetail: null
        }
    },
    created() {
        api.myNav().then(res => {
            this.menuList = [...res.data.data, ...res.data.data, ...res.data.data, ...res.data.data, ...res.data.data];
        })
    },
    methods: {
        menuEnter(item) {
            // console.log(item);
            this.curDetail = item;
        },
        menuLeave() {
            var self = this;
            this.timer = setTimeout(function () {
                self.curDetail = null;
            }, 200);

        },
        detailEnter() {
            clearTimeout(this.timer);
        },
        detailLeave() {
            this.curDetail = null;
        }
    }
}
</script>
<style>
.m-menu{
    width: 230px;
    height: 475px;
    box-sizing: border-box;
    margin-top: -60px;
    margin-left: 20px;
    color: #FFF;
    position: relative;
    background: linear-gradient(-180deg, rgba(2, 181, 157, .85) 2%, rgba(22, 146, 183, .85) 100%);
}
.m-menu dt{
    height: 50px;
    padding-top: 15px;
    box-sizing: border-box;
    font-size: 16px;
    font-weight: 400;
    margin-left: 15px;
    margin-bottom: 10px;
}
.m-menu dd{
    position: relative;
    box-sizing: border-box;
    padding: 2px 12px;
    height: 26px;
    cursor: pointer;
}
.m-menu dd:hover{
    background: rgba(255, 255, 255, .2);
}
.m-menu dd:hover i{
    opacity: 1;
}
.m-menu .nav{
    font-size: 13px;
}
.m-menu .nav dd{
    margin: 0;
}
.m-menu .nav dd i{
    font-family: 'meituan';
    font-size: 14px;
    font-style: normal;
    opacity: 0.3;
    margin-right: 11px;
}
.m-menu .nav dd i:before{
    width: 14px;
    height: 14px;
    display: inline-block;
}
.m-menu .nav .food:before{
    content: "\e622";
}
.m-menu .nav .takeout:before{
    content: "\e630";
}
.m-menu .nav .hotel:before{
    content: "\e62a";
}
.m-menu .nav .homestay:before{
    content: "\e631";
}
.m-menu .nav .movie:before{
    content: "\e62c";
    position: relative;
    left: -2px;
}
.m-menu .nav .airport:before{
    content: "\e632";
}
.m-menu .nav .ktv:before{
    content: "\e627";
}
.m-menu .nav .life:before{
    content: "\e633";
}
.m-menu .nav .hair:before{
    content: "\e626";
    transform: scale(0.7);
}
.m-menu .nav .marry:before{
    content: "\e629";
}
.m-menu .nav .offspring:before{
    content: "\e623";
}
.m-menu .nav .sport:before{
    content: "\e62b";
}
.m-menu .nav .furniture:before{
    content: "\e625";
}
.m-menu .nav .study:before{
    content: "\e624";
}
.m-menu .nav .health:before{
    content: "\e628";
}
.m-menu .nav .bar:before{
    content: "\e621";
}
.m-menu .nav .arrow{
    width: 4px;
    height: 4px;
    border-bottom: 1px solid #fff;
    border-right: 1px solid #fff;
    transform: rotate(-45deg);
    display: block;
    position: absolute;
    right: 13px;
    top: 0;
    bottom: 0;
    margin: auto;
}
.m-menu .detail{
    padding: 0 30px;
    position: absolute;
    left: 230px;
    bottom: 0;
    width: 400px;
    height: 416px;
    background: #fff;
    z-index: 9999;
}
.m-menu .detail h4{
    font-size: 16px;
    font-weight: 500;
    margin-top: 24px;
    height: 22px;
    line-height: 22px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e5e5e5;
    color: #222;
}
.m-menu .detail span{
    color: #999;
    font-size: 12px;
    line-height: 15px;
    display: inline-block;
    margin-right: 16px;
    margin-top: 10px;
    cursor: pointer;
}
.m-menu .detail span:hover{
    color: #31BBAC;
}
</style>


