<template>
    <div class="m-product-categroy">
        <dl class="classic">
            <dt>分类</dt>
            <dt>全部</dt>
            <dd v-for="item in classicList" :key="item.type">
                <m-select 
                    :name="item.title"
                    :list="item.sub_list"
                />
            </dd>
        </dl>
        <dl class="classic">
            <dt>区域</dt>
            <dt>全部</dt>
            <dd v-for="item in areaList" :key="item.type">
                <m-select 
                    :name="item.title"
                    :list="item.sub_list"
                />
            </dd>
        </dl>
    </div>
</template>
<script>
import MSelect from "./select.vue"
export default {
    components: {
        MSelect
    },
    data() {
        return {
            classicList: [
                {
                title: "美食",
                type: "food",
                sub_list: [
                    {
                    name: "日本菜",
                    id: "Japan"
                    },
                    {
                    name: "饮品店",
                    id: "drink"
                    },
                    {
                    name: "面包甜点",
                    id: "cate"
                    },
                    {
                    name: "面包甜点",
                    id: "cate"
                    }
                ]
                },
                {
                title: "酒店住宿",
                type: "hotal",
                sub_list: [
                    {
                    name: "温泉酒店",
                    id: "hot_spring"
                    },
                    {
                    name: "经济型酒店",
                    id: "economy"
                    },
                    {
                    name: "高端酒店",
                    id: "high"
                    }
                ]
                }
            ],
            areaList: [
                {
                title: "推荐商圈",
                sub_list: [
                    {
                    name: "望京",

                    id: 120000
                    },

                    {
                    name: "昌平",

                    id: 12222
                    }
                ]
                }
            ]
        }
    }
}
</script>
<style>
.m-product-categroy{
    padding: 15px 20px 0;
    background: #FFF;
    border: 1px solid #E5E5E5;
    border-radius: 4px;
    color: #333;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
}
.m-product-categroy .classic{
    display: flex;
    padding-bottom: 10px;
}
.m-product-categroy .classic dd{
    width: 80px;
}
.m-product-categroy .classic dt:nth-child(1) {
    margin-right: 52px;
}
.m-product-categroy .classic dt:nth-child(2) {
    border-radius: 100px;
    background: #13D1BE;
    color: #FFF;
    height: 22px;
    line-height: 22px;
    width: 40px;
    box-sizing: border-box;
    margin-right: 40px;
    text-align: center;
    cursor: pointer;
}
.m-product-categroy .classic:nth-child(1) dd{
    border-bottom: 1px solid #ddd;
}
</style>


