import axios from 'axios'
axios.defaults.baseURL = 'https://api.duyiedu.com';
// 请求拦截器
axios.interceptors.request.use(function(config) {
    config.params = {
        ...config.params,
        appkey: "17789278921_1585574287182"
    }
    return config;
}, function(error) {
    return Promise.reject(error);
})

export default axios;


