<template>
    <div class="page-login">
        <div class="login-header">
            <a href="http://www.meituan.com" class="logp"></a>
        </div>
        <div class="login-panel">
            <div class="banner">
                <img src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" width="480" height="370" alt="美团网">
            </div>
            <div class="form">
                <h4 v-if="error" class="tips">{{error}}</h4>
                <p class="biao">
                    <span class="zhang">
                        登录账号
                    </span>
                </p>
                <el-input :class="{error: error && !userName}"  v-model="userName" placeholder="手机号/用户名/邮箱" prefix-icon="profile"></el-input>
                <el-input :class="{error: error && !password}" v-model="password" type="password" placeholder="密码"  prefix-icon="password"></el-input>
                <div class="foot">
                    <a href="#">忘记密码？</a>
                </div>
                <el-button type="primary" class="btn-login" @click="submit">登录</el-button>
                <p class="reg">
                    <span class="hao">还没有账号？</span>
                    <router-link :to="{name: 'register'}">免费注册</router-link>
                </p>
                <div class="oauth-wrapper">
                    <h3 class="title-wrapper">
                        <span class="title">
                            用合作网站账号登录
                        </span>
                    </h3>
                    <div class="oauth cf">
                        <a class="oauth__link oauth__link--qq" href="/account/connect/tencent" data-mtevent="{&quot;la&quot;:&quot;oauth/qq&quot;}" target="_blank"></a>
                        <a class="oauth__link oauth__link--weibo" href="/account/connect/sina" data-mtevent="{&quot;la&quot;:&quot;oauth/sina&quot;}" target="_blank"></a>
                    </div>
                </div>
            </div>
        </div>
        <footer>
            <ul>
               <li><a href="#">关于美团</a></li>
               <li><a href="">加入我们</a></li>
               <li><a href="">商家入驻</a></li>
               <li><a href="">帮助中心</a></li>
               <li><a href="">美团手机版</a></li>
           </ul>
           <p>©2018 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号</p>
        </footer>
    </div>
</template>
<script>
import api from '@/api/index.js'
export default {
    data() {
        return {
            userName: '',
            password: '',
            error: ''
        }
    },
    methods: {
        submit() {
            if (!this.userName) {
                this.error = '请输入账号';
                return false;
            }
            if (!this.password) {
                this.error = '请输入密码';
                return false;
            }
            api.login({
                params: {
                    userName: this.userName,
                    password: this.password
                }
            }).then((res) => {
                console.log(res);
                if (res.data.status == 'success') {
                    this.$router.push({name: 'index'});
                    this.$store.commit('setUserName', this.userName);
                } else {
                    this.error = res.data.msg;
                }
            })
        }
    }
}
</script>
<style>
.page-login .login-header{
    position: relative;
    width: 980px;
    height: auto;
    margin: 40px auto 30px;
}
.page-login .login-header a{
    display: block;
    width: 147px;
    height: 54px;
    background-image: url(//s0.meituan.net/bs/fe-web-meituan/fa5f0f0/img/logo.png);
    background-position: 0 !important;
    background-size: contain !important;
}
.page-login .login-panel{
    margin: 0 auto 70px;
    width: 980px;
    display: flex;
}
.page-login .login-panel .banner{
    margin-right: 115px;
}



.page-login .login-panel .from{
    display: flex;
    flex-direction: column;
    width: 270px;
}
.page-login .login-panel .tips{
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #f5d8a7;
    border-radius: 2px;
    background: #fff6db;
    font-size: 12px;
    padding-left: 26px;
}
.page-login .login-panel .biao{
    font-size: 14px;
    margin: 0;
}
.page-login .login-panel .el-input{
    margin-top: 10px;
}
.page-login .login-panel .foot{
    margin-top: 10px;
    font-size: 12px;
    padding: 3px 0;
}
.page-login .login-panel .foot a{
    float: right;
    text-decoration: none;
    color: #FE8C00;
}
.page-login .login-panel .error input{
    border-color: red;
}
.page-login .login-panel.profole:after{
    font-family: iconfont;
    content: "\e627";
    font-style: normal;
    top: -1px;
    left: 5px;
    position: absolute;
}
.page-login .login-panel.password:after{
    font-family: iconfont;
    content: "\E600";
    font-style: normal;
    top: -1px;
    left: 3px;
    position: absolute;
    font-size: 18px;
}
.page-login .login-panel .form .btn-login{
    margin-top: 10px;
    color: #fff;
    background-color: #2db3a6;
    border: none;
    padding: 12px 15px;
    width: 100%;
}
.page-login .login-panel .reg span{
    font-size: 14px;
    color: #666;
}
.page-login .login-panel .reg a{
    color: #FE8C00;
    font-size: 14px;
    text-decoration: none;
}
.page-login .login-panel .oauth-wrapper{
    margin-top: 20px;
}
.page-login .login-panel .oauth-wrapper .title-wrapper{
    position: relative;
    margin-bottom: 30px;
    border-bottom: 1px solid #CCC;
    width: 100%;
    height: 0;
    overflow: visible;
}
.page-login .login-panel .oauth-wrapper .title-wrapper .title{
    position: absolute;
    top: -10px;
    left: 50%;
    margin-left: -75px;
    width: 150px;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: #666;
    background: #FFF;
}
.page-login .login-panel .oauth-wrapper .oauth{
    padding-left: 88px;
}
.page-login .login-panel .oauth-wrapper .oauth .oauth__link{
    float: left;
    margin-right: 40px;
    width: 18px;
    height: 18px;
    background-position: -1250px -491px;
    background-image: url(//s0.meituan.net/bs/file/?f=fe-sso-fs:build/assets/sp-normal.2ee5c09.png);
} 
.page-login .login-panel .oauth-wrapper .oauth .oauth__link--weibo{
    width: 18px;
    height: 18px;
    background-position: -1250px -599px;
    background-image: url(//s0.meituan.net/bs/file/?f=fe-sso-fs:build/assets/sp-normal.2ee5c09.png);
}



.page-login footer{
    width: 980px;
    margin: 0 auto 30px;
    text-align: left;
}
.page-login footer ul{
    border-top: 1px solid #EEE;
    border-bottom: 1px solid #EEE;
    padding: 12px 0;
    color: #ddd;
}
.page-login footer ul li{
    display: inline-block;
    font-size: 13px;
    text-align: center;
}
.page-login footer ul li:not(:last-child){
    border-right: 1px solid #eee;
}
.page-login footer ul li a{
    font-size: 13px;
    color: #999;
    padding-right: 10px;
    text-decoration: none;
}
.page-login footer ul li a:hover{
    text-decoration: underline;
}
.page-login footer ul li a:not(.first){
    padding-left: 10px;
}
.page-login footer>p{
    margin: 10px 0;
    font-size: 13px;
    color: #999;
}
</style>


