import Vue from 'vue'
import Router from 'vue-router'

import defaultPage from '@/layout/default.vue'//默认页面
import blankPage from '@/layout/blank.vue'//登录注册页面

import Index from '@/page/index.vue'//主页内容
import changeCity from '@/page/changeCity.vue'//切换城市页面
import goodsList from '@/page/goodsList.vue'//商品列表

import Login from '@/page/login.vue'//登录
import Register from '@/page/register.vue'//注册
Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'defaultPage',
      component: defaultPage,
      redirect: '/index',//重定向
      children: [
      {
        path: 's/:name',
        name: 'goods',
        component: goodsList
      },
      {
        path: '/index',
        name: 'index',
        component: Index
      },{
        path: '/changeCity',
        name: 'changeCity',
        component: changeCity
      }]
    },
    {
      path: '/blank',
      name: 'blankPage',
      component: blankPage,
      children: [{
        path: 'login',
        name: 'login',
        component: Login
      },{
        path: 'register',
        name: 'register',
        component: Register
      }]
    }
  ]
})



