<template>
    <div>
        <span class="name">按省份选择：</span>
        <m-select 
            :list="provinceList"
            :value="province"
            title="省份"
            :showWrapperActive='provinceActive'
            @change_active="changeProvinceActive"
            @change="changeProvince"
        />
        <m-select 
            :list="cityList"
            :value="city"
            title="城市"
            :showWrapperActive='cityActive'
            @change_active="changeCityActive"
            @change="changeCity"
        />
        <span>直接搜索：</span>
        <el-select
            v-model="searchWord"
            filterable
            remote
            reserve-keyword
            aria-placeholder="请输入关键字"
            :remote-method="remoteMethod"
            :loading="loading"
        >
            <el-option v-for="item in searchList" :key="item" :lable="item" :value="item"></el-option>
        </el-select>
    </div>
</template>
<script>
import MSelect from './select.vue'
import api from '@/api/index.js'
export default {
    components: {
        MSelect
    },
    data() {
        return {
            provinceList: ['山东', '陕西'],
            province: '省份',
            cityList: ['西安', '宝鸡'],
            city: '城市',
            provinceActive: false,
            cityActive: false,
            searchList: ['西安', '宝鸡'],
            searchWord: '',
            loading: false,
        }
    },
    // created() {
    //     api.myProvince().then(res => {
    //         this.provinceList = res.data.data;
    //     })
    // }
    methods: {
        changeProvinceActive(flag) {
            this.provinceActive = flag;
            if(flag) {
                this.cityActive = false;
            }
        },
        changeCityActive(flag) {
            this.cityActive = flag;
            if(flag) {
                this.provinceActive = false;
            }
        },
        remoteMethod(val) {
            //拿到输入框中的值
        },
        changeProvince(value) {
            this.province = value;
        },
        changeCity(value) {
            this.city = value;
        },
    }
}
</script>
<style scoped>

</style>



