<template>
    <div class="page-product">
        <el-row class="m-crumbs">
            <crumbs />
        </el-row>
        <el-row>
            <el-col :span="19">
                <el-row>
                    <categroy />
                </el-row>
                <el-row>
                    <list />
                </el-row>
            </el-col>
            <el-col :span="5">
                <el-row>
                    <div class="ditu">
                        地图
                    </div>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import crumbs from '@/components/products/crumbs.vue'
import Categroy from '@/components/products/categroy.vue'
import List from '@/components/products/list.vue'
export default {
    components: {
        crumbs,
        Categroy,
        List
    }
}
</script>
<style>
.page-product{
    margin-top: -30px;
    /* padding-top: 10px; */
    font-size: 14px;
}
.page-product .m-crumbs{
    margin: 10px 0 20px 0;
}
.ditu{
    width: 230px;
    height: 230px;
    background-color: #ccc;
    margin: 0 auto;
    font-size: 66px;
    color: #aaa;
    line-height: 230px;
    text-align: center;
}
</style>



