<template>
    <div class="layout-default">
        <el-container>
            <el-header style="height: 40px">
                <my-header />
            </el-header>            
            <el-main>
                <router-view></router-view>
            </el-main>  
            <el-footer>
                <my-footer />
            </el-footer>
        </el-container>
    </div>
</template>

<script>
import MyHeader from '@/components/header/index.vue'
import MyFooter from '@/components/footer/index.vue'
export default {
    components: {
        MyHeader,
        MyFooter
    }
}
</script>

<style>
.layout-default{
    -webkit-font-smoothing: antialiased;
    font-family: "-apple-system",
    BlinkMacSystemFont,
    Roboto,
    "Helvetica Neue",
    "MIcrosoft YaHei",
    sans-serif !important;
    background-color: #F8F8F8;
} 
.layout-default .el-header .m-header{
    width: 100%;
}
 .el-header,

 .el-footer{
    width: 1190px;
    margin: 0 auto;
    padding: 0;
    overflow: unset;
}
 .el-main{
     width: 1190px;
    margin: 0 auto;
    padding: 0;
 }
.el-header{
    background: #F8F8F8;
    color: #999;
    width: 100%;
    font-size: 12px;
    margin-bottom: 130px;
}
.el-header .m-header{
    width: 1190px;
    margin: 0 auto;
}
.el-main{
    /* overflow: unset; */
    /* margin-top: 35px; */
    padding-top: 50px !important;
}

</style> 


