<template>
    <div class="m-products-list">
        <ul>
            <li v-for="item in nav" :key="item.key">
                {{item.name}}
            </li>
        </ul>
        <el-row>
            <item 
                v-for="(item, index) in productList"
                :key="index"
                :meta="item"
            />
        </el-row>
    </div>
</template>
<script>
import Item from './item.vue'
import api from '@/api/index.js'
export default {
    components: {
        Item
    },
    data() {
        return {
            nav: [
                {
                key: "s-default",
                name: "智能排序",
                active: true
                },
                {
                key: "s-price",
                name: "价格最低",
                active: false
                },
                {
                key: "s-score",
                name: "人气最高",
                active: false
                },
                {
                key: "s-comment",
                name: "评价最高",
                active: false
                }
            ],
            productList: []
        }
    },
    created() {
    api.getProductsList().then(res => {
        this.productList = res.data.data;
    })

    }
}
</script>
<style>
.m-products-list{
    box-sizing: border-box;
    margin-top: 10px;
    background: #FFF;
    border: 1px solid #E5E5E5;
    border-radius: 4px;
    color: #333;
    font-size: 14px;
    line-height: 20px;
    padding: 11px 20px;
}
.m-products-list h3>a{
    text-decoration: none;
    color: #333;    
}
.m-products-list ul{
    padding-bottom: 2px;
    list-style: none;
    padding: 11px 0;
    margin: 0;
}
.m-products-list>ul li{
    width: 96px;
    height: 20px;
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    cursor: pointer;
}
.m-products-list ul li.s-nav-active{
    color: #31BCAD;
}
.m-products-list ul .s-price{
    position: relative;
}
.m-products-list ul .s-price:before,
.m-products-list ul .s-price:after{
    content: " ";
    position: absolute;
    right: 8px;
    top: 0;
    border: 4px solid transparent;
    border-bottom-color: #ddd;
    width: 0;
    height: 0;
}
.m-products-list ul .s-price:after{
    top: 12px;
    border-bottom-color: transparent;
    border-top-color: #ddd;
}
</style>


