<template>
    <div class="category">
        <dl class="m-categroy">
            <dt>按拼音首字母选择：</dt>
            <dd v-for="(item, index) in list" :key="index">
                <a :href="'#city-'+item">{{ item }}</a>
            </dd>
        </dl>
        <dl 
            class="m-categroy-section"
            v-for="(item, index) in cityGroup"
            :key="index"
            :id="'city-'+index"
        >
            <dt>{{index}}</dt>
            <dd> 
                <span 
                    v-for="city in item"
                    :key="city.id"
                >{{city.name}}</span>
            </dd>
        </dl>
    </div>
</template>
<script>
import api from '@/api/index.js'
export default {
    data() {
        return {
            list: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split(''),
            cityList: [],
            cityGroup: {}
        }
    },
    created() {
        api.getCityList().then(res => {
            this.cityList = res.data.data;
            // console.log(this.cityList)
            let obj = {};
            this.cityList.forEach((item, index) => {
                if(!obj[item.firstChar.toUpperCase()]) {
                    obj[item.firstChar.toUpperCase()] = [];
                }
                obj[item.firstChar.toUpperCase()].push(item);
            })
            // console.log(obj)
            this.cityGroup = obj;
        })
    },
    methods: {

    }
}
</script>
<style>
.m-categroy{
    top: 10px;
    display: flex;
    margin-bottom: 30px;
}
.m-categroy dt{
    font-size: 16px;
    color: #333;
    font-weight: 500;
}
.m-categroy dd{
    font-size: 15px;
    color: #666;
    margin: 0 6px;
    width: 24px;
    height: 24px;
    padding: 4px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border-radius: 50%;
}
a{
  text-decoration: none;
  color: #666;
}
.m-categroy dd:hover a{
    color: #13D1BE;
}
.m-categroy-section{
    display: flex;
    padding: 13px 30px 13px 10px;
    border-radius: 10px;
}
.m-categroy-section:hover{
    background: #F8F8F8;
}
.m-categroy-section dt{
    box-sizing: border-box;
    vertical-align: top;
    padding-top: 10px;
    display: inline-block;
    text-align: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    background: #13D1BE;
    box-shadow: 0 4px 5px 0 rgba(39, 178, 164, .22);
}
.m-categroy-section dd{
    flex: 1;
}
.m-categroy-section dd span{
    margin: 10px 20px;
    color: #666;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
}
.m-categroy-section dd span:hover{
    color:#13D1BE;
}
.m-categroy-section dd span:target{
    color: aquamarine;
}
</style>


